import { useState } from 'react';
import Button from '@mui/material/Button';
import AddIcon from '@mui/icons-material/Add';
import Cancel from '@mui/icons-material/Cancel';
import { Progress } from './Progress';

interface File {
  name: string
  type: string
}

interface Props {
  onSelectFile: (file: File) => void
}

export const FileUpload: React.FC<Props> = () => {
  const [fileDetails, setFileDetails] = useState<File | null>(null);

  const setFile = (event: any) => {
    console.log('event', event.target.files)
    console.log('filename', event.target.files[0].name)

    const fileSelected = {
      name: event.target.files[0].name,
      type: event.target.files[0].type,
    }

    setFileDetails(fileSelected);
  }

  return (
    <div style={{ display: 'block', textAlign: 'center' }}>
      <div style={{ height: '50px' }}>
        <Progress />
      </div>
      <div style={{ height: '50px' }}>{fileDetails && <p>Upload: {fileDetails.name}</p>}</div>
      <Button variant="contained" component="label" color="primary">
        {" "}
        <AddIcon /> Upload a file
        <input type="file" hidden onChange={setFile} />
      </Button>
      <Button variant="contained" component="label" color="error" style={{ marginRight: '10px' }}>
        {" "}
        <Cancel /> Cancel
      </Button>
      <Button variant="contained" component="label" color="primary">
        {" "}
        <AddIcon /> Send File
      </Button>
    </div>
  );
}
